import { Component } from '@angular/core';
import { IonicPage, ViewController, ModalController } from 'ionic-angular';

import { CareGiverProvider, CareGiver } from '../../providers/care-giver/care-giver';
import { EditCareGiverModalPage} from "../edit-care-giver-modal/edit-care-giver-modal";

@IonicPage()
@Component({
  selector: 'page-caregivers',
  templateUrl: 'care-giver-modal.html',
})
export class CaregiversModalPage {

  careGivers: CareGiver[];

  selector : string;

  constructor(
    public careGiverProvider: CareGiverProvider,
    private modalCtrl: ModalController,
    public viewCtrl: ViewController) {

      this.selector = new Date(Date.now()).toISOString();
      this.loadCareGiversIntoPageContext();
  }

  cancel() {
    this.viewCtrl.dismiss();
  }
  

  openAddCareGiver() {
    let modal = this.modalCtrl.create("AddCaregiverModalPage", {});
    modal.onDidDismiss(extraCareGiver => {
      if ("undefined" !== typeof extraCareGiver) {
        this.careGivers.push(extraCareGiver);
      }
    });
    modal.present();

  }

  openEditCareGiverModal(selectedCareGiver: CareGiver) {
    let modal = this.modalCtrl.create(EditCareGiverModalPage, {"selectedCareGiver": selectedCareGiver});
    modal.onDidDismiss(editedCareGiver => {
      if ("undefined" !== typeof editedCareGiver) {
        this.careGivers.filter(it => it.code == editedCareGiver.code)
          .forEach(it => {
            it.name= editedCareGiver.name;
          });
      }
    });

    modal.present();
  }


  switchActiveState(careGiver) {

    this.careGiverProvider.updateCareGiver(careGiver);


  }

  private loadCareGiversIntoPageContext() {
    this.careGiverProvider.getAllCareGivers().then((loadedCareGivers) => {
      this.careGivers = loadedCareGivers;
    });
  }


}



